import React from "react";
import CodeGenerator from "./CodeGenerator.js";

export default function Card({props}) {
  function importAll(r) {
    let images = {};
    r.keys().forEach((item, index) => { images[item.replace('./', '')] = r(item); });
    return images
  }
  const icons = importAll(require.context('../Assets/logo', false, /\.(png|jpe?g|svg)$/));
  
  return (
    <div className="carousel__item">
      <div className="carousel__item-body">
        <div className="carousel__item-icon">
          {props.icon?<img src={icons[props.icon]} alt={props.icon}/>:<React.Fragment/>}
        </div>
        <div className="carousel__item-head">
          <p className="title">{props.title || ""}</p>
        </div>
        {props.codeData?<div className="card-code" id={props.id}>
          <CodeGenerator props={props.codeData}/>
          <div className="card-code-num">{props.number}</div>
        </div>:<div className="card-code"></div>}
      </div>
    </div>
  );
}



